import * as yup from "yup";

export const schema = yup
  .object({
    name: yup
      .string()
      .min(4, "     4 ")
      .max(64, "     64 ")
      .required("  "),
    text: yup
      .string()
      .min(6, "     6 ")
      .max(200, "     200 ")
      .required("  "),
    captcha: yup
      .string()
      .min(5, "   5 ")
      .max(5, "   5 ")
      .required("  "),
    file: yup
      .mixed()
      .test({
        name: "size-file",
        message: "     5 ",
        test: (value: FileList) => {
          if (value && value.length > 0) {
            return value[0]?.size < 1024 * 1024 * 5;
          }
          return true;
        },
      })
      .test({
        name: "format-file",
        message: "  jpg, jpeg  png",
        test: (value: FileList) => {
          if (value && value.length > 0) {
            return /\.(jpg|jpeg|png)$/i.test(value![0]?.name);
          }
          return true;
        },
      }),
  })
  .required();
